#header {
    width: 100%; /* Garante que ocupe toda a largura da tela */
    height: 10vh; /* Melhor usar unidades relativas como vh para layouts responsivos */
    background-color: #141414;
    display: flex; /* Torna o header um contêiner flexível */
    align-items: center; /* Centraliza verticalmente o conteúdo */
    justify-content: space-between; /* Distribui o conteúdo com espaço entre os itens */
    padding: 0 20px; /* Adiciona espaçamento interno para evitar que os itens fiquem muito próximos da borda */
    box-sizing: border-box; /* Inclui o padding no cálculo da largura */
}
.logo-icon{
    width: 10%;
}
#header .logo-icon{
    margin-left: 10%;
    height: 95%;
    margin-bottom: 4px;
}
.header-nav{
    
    display: flex;
    width: 35%;
    flex-direction: column;
    justify-content: space-evenly;
    list-style-type: none;
    margin-right: 10%;
}
.header-nav a{
    margin: 5px;
    position: relative;
    text-decoration: none;
    font-size: 1.3em;
    color: #e7eeeb;
    font-weight: 800;
}
.header-nav a::after{
    content: '';
    position: absolute;
    background-color: #eef6f3;
    height: 3px;
    width: 0%;
    left: 0;
    bottom: -7px;
    transition: 0.3s;
}
a:hover:after{
    width: 100%;
}
.nav-wrapper{
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.mobile-nav-wrapper{
   display: flex;
    flex-direction: column;
    background-color: black;
    z-index: 100;
    position: absolute;
    top: 10%;
    right: 0;
}
.mobile-nav-wrapper img{
    width: 50px;
    height: 50px;
}
.mobile-nav-wrapper a{
    height: 100px;
 }
.nav-icon{
    display: none;
    width: 40%;
    
    align-self: flex-end;
}

@media (min-width: 700px) and (max-width: 1000px){
    .header-nav a{
        font-size: 1.1em;
    }
}
@media (max-width: 700px){
    .nav-wrapper{
        display: none;
    }
    .nav-icon{
        display: flex;
    }
    .header-nav{
        margin-right: 5%;
    }
    
}
